@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&family=Roboto&display=swap);
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: "Fira Sans", sans-serif;
  background-color: #344e41;
}

.page-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  -webkit-align-items: center;
          align-items: center;
}

.content-wrap {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.AboutMe {
  position: absolute;
  left: 50%;
  top: 45%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 10px;
  color: rgba(255, 255, 255, 0.8);
  height: 60vh;
  width: 60vw;
}

.profile-picture {
  justify-self: center;
  margin-right: 20px;
}

.profile-picture img {
  border: 2px solid #a3b18a;
  width: 250px;
  height: 250px;
}

.profile-text {
  width: 400px;
  margin-left: 20px;
  font-size: 0.9rem;
  line-height: 1.5rem;
  text-align: left;
}

h2 {
  color: #a3b18a;
}

p i {
  color: #a3b18a;
  font-size: 0.9rem;
}

.fa-quote-left {
  margin-right: 0.6rem;
}

.fa-quote-right {
  margin-left: 0.6rem;
}

@media (max-width: 767.98px) {
  .AboutMe {
    -webkit-flex-direction: column;
            flex-direction: column;
    top: 50%;
  }

  .profile-picture {
    margin-right: 0px;
  }

  .profile-picture img {
    height: 220px;
    width: 220px;
  }

  .profile-text {
    font-size: 0.8rem;
    width: 80vw;
  }
}

.Project {
  margin-bottom: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: rgba(255, 255, 255, 0.8);
}

.project-description {
  font-size: 0.8rem;
  margin-left: 2rem;
  max-width: 650px;
}

.project-description-title {
  color: #a3b18a;
  font-size: 1.1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 15px;
}

.project-description-title a {
  color: #c0d1a2;
}
.project-description-title a:hover {
  color: #eeffd0;
}

.project-description-techstack {
  font-size: 0.9rem;
}

img {
  border: 2px solid #a3b18a;
  border-radius: 50%;
  width: 180px;
  height: 180px;
  box-shadow: inset -1px 3px 8px 5px #344e41,
    2px 5px 16px 0px rgba(0, 0, 0, 0.4), 5px 5px 15px 5px rgba(0, 0, 0, 0);
}

@media (max-width: 767.98px) {
  .Project {
    margin-top: 0px;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .project-description-title {
    font-size: 1rem;
    margin-top: 30px;
  }

  .project-description {
    font-size: 0.8rem;
    width: 80vw;
    margin-bottom: 20px;
    margin-left: 0px;
  }
}

.Projects {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 80vw;
  height: 75vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  overflow: auto;
}

ul {
  list-style: none;
}

.NavBar {
  margin-right: 80px;
  margin-top: 20px;
  position: absolute;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.NavBar-link {
  margin-right: 30px;
}

.active-link {
  color: #c0d1a2;
  border-bottom: 1px solid #c0d1a2;
}

a {
  color: #a3b18a;
  text-decoration: none;
}

a:hover {
  color: #c0d1a2;
}

@media (max-width: 767.98px) {
  .NavBar {
    margin-right: 0px;
    position: relative;
    right: auto;
    right: initial;
    -webkit-align-content: center;
            align-content: center;
  }

  .NavBar-link {
    margin-left: 15px;
    margin-right: 15px;
  }
}

.Footer {
  color: #a3b18a;
  font-size: 2.3rem;
  position: relative;
  bottom: 0;
  width: 30%;
  padding-bottom: 5px;
}

.social-media-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.copyright {
  margin-top: 10px;
  font-size: 0.6rem;
  text-align: center;
  opacity: 0.2;
}

